<template>
  <div class="main">
	   <div v-html="state.headerText"></div>
     <textarea spellcheck="false" ref="scriptInput" id="scriptInput" @keydown="processInput()" @keypress="processInput()" @input="processInput()"></textarea>
		 <div id="debug" ref="debug"></div>
  </div>
</template>

<script>
export default {
  name: 'Main',
  props: [ 'state' ],
	data(){
		return {
			goodClass: 'goodClass',
			badClass: 'badClass',
			oscript: ''
		}
	},
	methods:{
		redraw(oldval, newval){
			let oldTokens = this.tokenize(oldval.trim())
			let newTokens = this.tokenize(newval.trim())
      console.log(oldTokens, newTokens)
			console.log("\n\n\n")
      let out = ''
			for(let i=0;i<oldval.length;++i){
				let v = oldval[i]
				let highlight = false
				let fresh = true
				oldTokens.map((q, j)=>{
					if(!fresh) return
					if(q.pos == i){
						fresh = false
						highlight = true
					  out+='<span class="'+(newTokens[j].value==q.value?'good':'bad')+'Class">'
						i+=q.value.length-1
						out+=q.value=="\n"?"<br>":q.value
					}
				})
				if(highlight){
					out += '</span>'
				}
				if(v == "\n") out += '<br>'
        if(v == " ") out += '&nbsp;'
			}
			this.$refs['debug'].innerHTML = out
		},
		getCorrection(){
      if(!this.state.scriptInput.value){
        this.$refs['debug'].innerHTML = ''
        return
      }
      let sendData = {text: this.state.scriptInput.value}
      fetch('https://'+this.state.rootDomain+'/autosyntax/autocorrect.php', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(sendData),
      }).then(res=>res.text()).then(data=>{
        if(data){
					this.redraw(sendData.text, data)
        } else {
					this.redraw('', '')
				}
      })
		},
		tokenize(str){
      if(!str) return []
		  let tokens = []
			let token = {pos: 0, value: ''}
      str.split('').map((v, i) => {
         if((/^[a-zA-Z]/).test(v)){
				   if((/^[a-zA-Z]/).test(token.value[token.value.length-1])) token.value  += v
						 else{
               tokens=[...tokens, token]
               token = {pos: i, value: v}
						 }
         }else{
           tokens=[...tokens, token]
           token = {pos: i, value: v}
         }
      })
      if(token) tokens=[...tokens, token]
			return tokens
		},
		processInput(){
			let script = this.state.scriptInput.value
			if(!script || this.oscript == script) return
			this.oscript = script
      this.$refs['debug'].innerHTML=''
			let tokens = this.tokenize(script)
			if(tokens.length){
				this.state.tokens = tokens.map(v=>{
				  return {
					  token: v,
				  	stale: true,
		    		correction: '',
		  		}
				})
			} else {
				this.state.tokens = []
				this.tokens = []
				this.$refs['debug'].innerHTML=''
			}
			this.getCorrection()
		}
	},
	mounted(){
		this.state.scriptInput = this.$refs['scriptInput']
		this.state.scriptInput.focus()
	}
}
</script>

<style>
.main{
  margin: 10px;
	padding: 10px;
	border: 1px solid #4f86;
}
#scriptInput{
  background: #333;
	color: #ff0;
  width: calc(100% - 5px);
	height: calc(100vh - 70px);
}
#scriptInput:focus{
  outline: none;
}
#debug{
  position: absolute;
	top: 50%;
	width: 100%;
	height: calc(50% - 12px);
	border: 1px solid red;
}
.goodClass{
  background: #4f86;
	display: inline-block;
}
.badClass{
	background: #f486;
	display: inline-block;
}
</style>
